@use 'base/variables' as v;
@use 'base/mixins' as m;

.site-header{
    @include m.bgImage('../img/header.avif', v.$secundarioOpacity);
    padding: 5rem 0;

    .logo{
        max-width: 20rem;
        margin: 0 auto;

        @include m.tablet {  
            margin: 0;
        }
    }
    .contenido-header{
        padding-top: 10rem 0;
        @include m.desktop{
            padding-top: 15rem 0;
        }

        h1{
            color: v.$blanco;
            font-size: 4.5rem;
            @include m.tablet {  
                font-size: 6rem;
                max-width: 70%;
            }
            @include m.desktop {  
                max-width: 40%;
            }
        }
    }
}