@use 'base/variables' as v;
@use 'base/mixins' as m;


.planes{
    background-color: v.$secundario;
    h2{
        @include m.HeadingBorder(v.$blanco, v.$primario);
    }
    .descripccion{
        color: v.$blanco,;
    }
}

.nuetros-planes{
    display: flex;
    flex-direction: column;
    @include m.tablet{
        flex-direction: row;
        max-width: 40rem;
        margin-inline: -10rem;
        margin-block-start: 10rem;
    }
    @include m.desktop {  
        flex-direction: row;
        max-width: 80rem;
        margin-inline: auto;
        margin-block-start: 10rem;
    }

    .plan{
        flex-grow: 1;
        background-color: v.$blanco;
        padding: 16rem 4rem 4rem 4rem;
        border-bottom: 2px solid #cdcbcb;
        @include m.tablet {  
            border: none;
            transition-property: transform;
            transition-duration: 300ms;
            padding: 16rem 2.7rem 4rem 2.7rem;
            &:hover{
                transform: scale(1.1);
            }
        }
        @include m.desktop{
            padding: 16rem 4rem 4rem 4rem;
        }

        .precio{
            font-size: 1.8rem;
            text-align: center;
            font-weight: 700;
            text-transform: uppercase;
        }

        .listado{
            li{
                text-align: center;
                margin-bottom: 2rem;
                &:last-of-type{
                    margin-bottom: 0;
                }
            }
        }

        &.inicio{
            @include m.IconoBg('../img/icono_inicio.svg');
        }

        &.negocio{
            background-color: v.$primario; 
            order: -1;
            @include m.IconoBg('../img/icono_negocio.svg');
            @include m.tablet {  
                order: unset;
                margin: -5rem 0;
                border-radius: 2rem;
                background-position: 50% 7rem;
                padding-top: calc(16rem + 5rem);
                z-index: 1;
            }
        }
        &.pro{
            @include m.IconoBg('../img/icono_pro.svg');
        }
    }
}